body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar {
  background-color: #B22506;
  position: fixed;
  top: 55px;
  width: 100%;
  height: 56px;
  transition: top 1s; }
  @media only screen and (min-width: 721px) {
    .navbar {
      top: 0; } }
  .navbar .navbar-container {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    overflow: hidden;
    display: flex; }
    .navbar .navbar-container .navbar__logo {
      display: none; }
      @media only screen and (min-width: 721px) {
        .navbar .navbar-container .navbar__logo {
          display: block;
          width: 50%;
          font-family: "Fredericka the Great", cursive;
          font-size: 48px;
          line-height: 56px;
          text-align: left; } }
    .navbar .navbar-container .navbar__buttons {
      width: 100%;
      display: flex;
      justify-content: space-around;
      line-height: 56px;
      font-family: "Permanent Marker", cursive;
      font-size: 22px;
      cursor: pointer; }
      @media only screen and (min-width: 721px) {
        .navbar .navbar-container .navbar__buttons {
          width: 50%; } }
      .navbar .navbar-container .navbar__buttons .navbar__button {
        display: block; }

.navbar--transition {
  top: 0; }

.logo {
  background-image: linear-gradient(to top, #B22506, #e32f08);
  position: fixed;
  width: 100%;
  display: block;
  transition: display;
  height: 56px;
  transition: height 1s; }
  @media only screen and (min-width: 721px) {
    .logo {
      display: none; } }
  .logo .logo-container {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    overflow: hidden;
    font-family: "Fredericka the Great", cursive;
    font-size: 48px;
    line-height: 56px; }

.logo--transitioned {
  height: 0; }

.home {
  height: calc(100vh - 100px);
  max-height: 800px;
  background-image: url(/knives/static/media/home.d180e1a1.jpeg);
  background-repeat: no-repeat;
  background-position: -100px 56px;
  padding-top: 110px;
  display: flex;
  align-items: center; }
  @media only screen and (min-width: 721px) {
    .home {
      background-position: center 56px; } }
  .home .home-container {
    display: block;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around; }
    @media only screen and (min-width: 721px) {
      .home .home-container {
        width: 400px;
        margin: 0 auto; } }
    .home .home-container .home__text {
      display: none; }
      @media only screen and (min-width: 721px) {
        .home .home-container .home__text {
          display: block;
          margin: 0 auto;
          height: 80%;
          font-size: 68px;
          font-family: "Fredericka the Great", cursive; } }
    .home .home-container .home__button {
      cursor: pointer;
      display: block;
      line-height: 40px;
      margin: 0 auto;
      width: 200px;
      height: 40px;
      background-color: #B22506;
      color: white;
      font-weight: bolder; }

.about .about-container {
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
  height: 100vh; }
  @media only screen and (min-width: 1024px) {
    .about .about-container {
      display: flex;
      height: 800px; } }
  .about .about-container .about__text {
    overflow: hidden; }
    @media only screen and (min-width: 1024px) {
      .about .about-container .about__text {
        width: 50%; } }
    .about .about-container .about__text .text-container {
      text-align: left;
      box-sizing: border-box;
      margin-left: -600px;
      transition: margin-left 2s; }
      .about .about-container .about__text .text-container .text__header {
        font-size: 48px;
        color: #cb2a07;
        margin-bottom: 10px; }
      .about .about-container .about__text .text-container .text__header--small {
        margin-top: 10px;
        font-size: 32px;
        color: #cb2a07; }
    .about .about-container .about__text .text-container--transitioned {
      margin-left: 0; }
    .about .about-container .about__text .text-container--box {
      font-size: 22px;
      text-align: left;
      overflow: hidden;
      margin-left: 400px;
      transition: margin-left 3s; }
      .about .about-container .about__text .text-container--box .text__paragraph {
        margin-top: 30px;
        color: black; }
    .about .about-container .about__text .box--transitioned {
      margin-left: 0; }
  .about .about-container .about__image {
    display: none; }
    @media only screen and (min-width: 1024px) {
      .about .about-container .about__image {
        margin-left: 30px;
        display: block;
        width: 50%;
        overflow: hidden;
        background-repeat: no-repeat; }
        .about .about-container .about__image .image__picture {
          background-image: url(/knives/static/media/about.6eac9fe7.jpg);
          max-width: 550px;
          height: 100%;
          margin: 30px;
          margin-left: -600px;
          transition: margin-left 2s; }
        .about .about-container .about__image .image__picture--transitioned {
          margin-left: 0; } }

.banner {
  height: 50vh;
  max-height: 600px; }
  .banner .banner-container {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    overflow: hidden;
    display: block;
    height: 100%;
    margin-top: 30px;
    background-image: url(/knives/static/media/banner.9ce2a027.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center; }
    .banner .banner-container .banner__text {
      color: #B22506;
      font-size: 56px;
      font-family: "Permanent Marker", cursive; }

.products .products-container {
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
  display: block; }
  .products .products-container .products__box {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 20px; }
    @media only screen and (min-width: 721px) {
      .products .products-container .products__box {
        flex-direction: row;
        justify-content: flex-start;
        height: 50vh;
        max-height: 600px; } }
    .products .products-container .products__box .products__product {
      cursor: pointer;
      width: 70%;
      margin: 0 auto; }
      .products .products-container .products__box .products__product .product__photo {
        background-image: url(/knives/static/media/product.4eda8094.jpeg);
        background-position: center;
        height: 150px;
        width: 150px;
        margin: 0 auto;
        border-radius: 50%;
        margin-top: 5px; }
        @media only screen and (min-width: 721px) {
          .products .products-container .products__box .products__product .product__photo {
            height: 220px;
            width: 220px; } }
        .products .products-container .products__box .products__product .product__photo .product__price {
          display: none;
          color: red;
          font-size: 30px;
          line-height: 100px;
          font-weight: bolder; }
          @media only screen and (min-width: 721px) {
            .products .products-container .products__box .products__product .product__photo .product__price {
              line-height: 150px;
              font-size: 48px; } }
          .products .products-container .products__box .products__product .product__photo .product__price span {
            margin-left: 4px; }
      .products .products-container .products__box .products__product .product__photo:hover {
        opacity: 0.5; }
        .products .products-container .products__box .products__product .product__photo:hover .product__price {
          display: inline-block; }

.footer {
  width: 100%;
  overflow: hidden;
  background-color: #B22506;
  height: 20vh;
  max-height: 180px; }
  .footer .footer-container {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    justify-content: space-around; }
    .footer .footer-container .footer__col {
      font-size: 18px;
      color: white;
      margin-top: 10px; }
    .footer .footer-container .footer__col--logo {
      font-family: "Fredericka the Great", cursive;
      font-size: 48px;
      width: 40%;
      cursor: pointer; }

